import { axiosInstance as request } from './axios';

// 
export function guestLogin(data = {}) {
    return request({
        url: '/auth/guestV2',
        method: 'post',
        data,
    });
}

// 
export function getToUid(data = {}) {
    return request({
        url: '/app/guest-id',
        method: 'post',
        data,
    });
}